html, body {
  height: 100%;
}

h1 {
  color: white;
  font-weight: bold;
}

.App {
  min-height: 100vh;
  position: relative;
  padding-bottom: 55px;
}

.btn {
  vertical-align: baseline;
  border-color: #FFFFFF;
  color: #FFFFFF;
  font-size: 125%;
}

.jumboBg {
  background-image: url("https://images.unsplash.com/photo-1541673513302-6144bb1a498d?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80");
  border-radius: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.4);
}

.jumboText {
  background: rgb(204, 204, 204); /* Fallback for older browsers without RGBA-support */
  background: rgba(0, 0, 0, 0.4);
  border-radius: 20px;
}

.jumboP {
  color: white;
  font-weight: bold;
  
}
